
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import Room from "../api/room";
import User from "../api/user";

export default {
components: { ViewBox, XHeader, Tabbar, Cell, Group },
data() {
return {
rooms: []
};
},
methods: {
joinRoom(roomNo) {
console.log(roomNo)
var _this = this;
if (!User.loginFilter(this)) {
return;
}

if (_this.user.room_no && roomNo != _this.user.room_no) {

_this.$vux.confirm.show({
title: "提示",
content: "是否退出当前所在房间",
onCancel() {},
onConfirm() {

Room.outRoom(_this, function() {

_this.user.room_no = 0;
_this.$router.push({
path: "/room",
query: { room_no: roomNo }
});
});
}
});
} else {
_this.$router.push({path:'/room', query:{'room_no':roomNo}})
}
}
},
mounted() {
var _this = this;

Room.roomList(_this, rooms => {
this.$set(this, "rooms", rooms);
});
}
};



